*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  align-items: center;
}
.container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 95%;
  height: 100vh;
}
#btn{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 10vw;
  height: 8vh;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  color:  white;
  font-size: medium;
  font-weight: bold;
  background-color: rgb(255, 81, 0);
}
#btn:hover{
  background-color: rgb(233, 77, 4);
}



